body{
    background: radial-gradient(#e5e5e5, #ffff, #e5e5e5);
}

@keyframes bounceIn {
    0% {
      transform: scale(0.1);
      opacity: 0;
    }
    50% {
        transform: scale(0.4);
      }
    100% {
      transform: scale(1);
    }
  }

.card{
    animation: bounceIn 3s;
    width: 20rem;
}

.card:hover{
    box-shadow: 5px 10px 20px 1px rgba(0, 0, 0, 0.253)!important;
}

.card-body{
    padding: 3rem 0 !important;
}

.card-text{ 
    
font-size:0.9rem;
padding: 0.4rem 1.9rem;

}

.overflow{
    overflow:hidden;
}

.container-fluid .row{
    padding-top: 6rem;
}

.card-img-top{
    transform: scale(1);
    transition: transform 0.5s ease;
}

.card-img-top:hover{
    transform: scale(1.8);
}